.editor-styles-wrapper .btn {
  background-color: var(--wp--preset--color--primary);
  border-color: var(--wp--preset--color--primary);
  color: var(--wp--preset--color--black);

  &:hover,
  &:active,
  &:focus {
    border-color: var(--wp--preset--color--primary-state);
    background-color: var(--wp--preset--color--primary-state);
    color: var(--wp--preset--color--black);
  }
}

.editor-styles-wrapper .btn--white,
.editor-styles-wrapper .btn.is-style-light {
  background-color: var(--wp--preset--color--white);
  border-color: var(--wp--preset--color--black);
  color: var(--wp--preset--color--black);

  &:hover,
  &:focus {
    background-color: var(--wp--preset--color--grey-light);
    border-color: var(--wp--preset--color--grey-light);
    color: var(--wp--preset--color--black);
  }
}

.editor-styles-wrapper .wp-block-file a.wp-block-file__button,
.editor-styles-wrapper .wp-block-button.is-style-default .wp-block-button__link {
  background-color: var(--wp--preset--color--primary);
  border-color: var(--wp--preset--color--primary);
  color: var(--wp--preset--color--black);

  &:hover,
  &:active,
  &:focus {
    border-color: var(--wp--preset--color--primary-state);
    background-color: var(--wp--preset--color--primary-state);
    color: var(--wp--preset--color--black);
  }
}

.editor-styles-wrapper .wp-block-button.is-style-light .wp-block-button__link {
  background-color: var(--wp--preset--color--white);
  border-color: var(--wp--preset--color--black);
  color: var(--wp--preset--color--black);

  &:hover,
  &:active,
  &:focus {
    background-color: var(--wp--preset--color--black);
    border-color: var(--wp--preset--color--black);
    color: var(--wp--preset--color--white);
  }
}

.editor-styles-wrapper .btn.btn--dark {
  background-color: var(--wp--preset--color--black);
  border-color: var(--wp--preset--color--black);
  color: var(--wp--preset--color--white);

  &:hover,
  &:active,
  &:focus {
    background-color: var(--wp--preset--color--white);
    border-color: var(--wp--preset--color--black);
    color: var(--wp--preset--color--black);
  }
}
