.is-style-style-2 .customCard-content .btn {
  background-color: var(--wp--preset--color--primary);
  border-color: var(--wp--preset--color--primary);
  color: var(--wp--preset--color--black);

  &:hover,
  &:active,
  &:focus {
    background-color: var(--wp--preset--color--primary-state);
    border-color: var(--wp--preset--color--primary-state);
    color: var(--wp--preset--color--black);
  }
}

.is-style-style-3 {
  background-color: var(--wp--preset--color--primary);
  color: var(--wp--preset--color--black);
}

.is-style-style-3 textarea.customCard-label,
.is-style-style-3 .customCard-content,
.is-style-style-3 .customCard-content textarea {
  background-color: var(--wp--preset--color--primary);
  color: var(--wp--preset--color--grey-dark);
}

.is-style-style-3 .customCard-content .btn {
  background-color: var(--wp--preset--color--grey-dark);
  border-color: var(--wp--preset--color--grey-dark);
  color: var(--wp--preset--color--color--white);

  &:hover,
  &:active,
  &:focus {
    background-color: var(--wp--preset--color--grey-darker);
    border-color: var(--wp--preset--color--grey-darker);
    color: var(--wp--preset--color--color--white);
  }
}

.is-style-style-3 .customCard-figure {
  background-color: var(--wp--preset--color--primary);
}
